import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import actions from './actions';
import actionHandlers from './action-handlers';
import styles from './styles.scss';

createCustomElement('sn-response-templates', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		/**
		 * Table name of the record to search against
		 *
		 * @type {string}
		 */
		table: { default: '' },
		/**
		 * Record sys_id
		 *
		 * @type {string}
		 */
		sysId: { default: '' },
		/**
		 * Channel to use for lookup
		 *
		 * @type {string}
		 */
		channel: {
			default: '',
			schema: {
				type: 'string'
			}
		}
	},
	initialState: {
		search: '',
		loading: false,
		isFullView: false,
		fullViewTemplateIndex: null,
		templates: []
	},
	actions,
	actionHandlers,
	styles
});

