import { actionTypes } from '@servicenow/ui-core';
import {
	RESPONSE_TEMPLATES_FETCH_SUCCEEDED,
	RESPONSE_TEMPLATES_FETCH_FAILED,
	RESPONSE_TEMPLATES_FETCHED,
	RESPONSE_TEMPLATES_FETCH_STARTED,
	NOW_CARD_CLICKED
} from '../common/constants';

import {
	responseTemplatesFetched,
	responseTemplatesFetchStarted,
	responseTemplatesFetchSucceeded,
	responseTemplatesFetchFailed,
	handleComponentBootstrapped
} from '../common/action-handlers';

import { RESPONSE_TEMPLATE_SELECTED } from './constants';

const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

const handleComponentPropertyChanged = ({ action, dispatch }) => {
	const { payload: { name }} = action;
	if (name === 'search')
		dispatch(RESPONSE_TEMPLATES_FETCHED);
};

export default {
	[RESPONSE_TEMPLATES_FETCHED]: responseTemplatesFetched,
	[RESPONSE_TEMPLATES_FETCH_STARTED]: responseTemplatesFetchStarted,
	[RESPONSE_TEMPLATES_FETCH_SUCCEEDED]: responseTemplatesFetchSucceeded,
	[RESPONSE_TEMPLATES_FETCH_FAILED]: responseTemplatesFetchFailed,

	[NOW_CARD_CLICKED]: ({ dispatch, action }) => {
		const { payload: { template }} = action;

		dispatch(RESPONSE_TEMPLATE_SELECTED, { template });
	},

	[COMPONENT_BOOTSTRAPPED]: handleComponentBootstrapped,
	[COMPONENT_PROPERTY_CHANGED]: handleComponentPropertyChanged
};
