import { createHttpEffect } from '@servicenow/ui-effect-http';

import {
	RESPONSE_TEMPLATES_FETCHED,
	RESPONSE_TEMPLATES_FETCH_STARTED,
	RESPONSE_TEMPLATES_FETCH_SUCCEEDED,
	RESPONSE_TEMPLATES_FETCH_FAILED
} from './constants';

export const responseTemplatesFetchStarted = createHttpEffect('api/sn_templated_snip/response_templates/get_templates', {
	method: 'POST',
	dataParam: 'data',
	successActionType: RESPONSE_TEMPLATES_FETCH_SUCCEEDED,
	errorActionType: RESPONSE_TEMPLATES_FETCH_FAILED
});

export const responseTemplatesFetchSucceeded = ({ action, updateState }) => {
	try {
		const templates = JSON.parse(action.payload.result);
		const validPayload = templates && Array.isArray(templates);
		if (validPayload) {
			updateState({ templates, loading: false });
			return;
		}
	} catch (err) {
		console.error(err);
	}
	updateState({ loading: false });
};

export const responseTemplatesFetchFailed = ({ action: { payload } }) => {
	console.error('ERROR', payload.error);
};

export const responseTemplatesFetched = ({ properties, dispatch, updateState }) => {
	updateState({ loading: true });
	dispatch(RESPONSE_TEMPLATES_FETCH_STARTED, {
		data: {
			tableName: properties.table,
			recordId: properties.sysId,
			searchTerm: properties.search,
			limit: 50,
			offset: 0,
			includeEvaluatedBody: true,
			opts: {
				channel: properties.channel
			}
		}
	});
};

export const handleComponentBootstrapped = ({ dispatch }) => {
	dispatch(RESPONSE_TEMPLATES_FETCHED);
};
