import { actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import {
	RESPONSE_TEMPLATES_FETCH_SUCCEEDED,
	RESPONSE_TEMPLATES_FETCH_FAILED,
	RESPONSE_TEMPLATES_FETCHED,
	RESPONSE_TEMPLATES_FETCH_STARTED
} from '../common/constants';

import {
	responseTemplatesFetched,
	responseTemplatesFetchStarted,
	responseTemplatesFetchSucceeded,
	responseTemplatesFetchFailed,
	handleComponentBootstrapped
} from '../common/action-handlers';

import {
	SEARCH_INPUT_CHANGED,
	NOW_CARD_HEADER_ACTION_CLICKED,
	COPY_TO_CLIPBOARD
} from './constants';

import { NOW_CARD_CLICKED } from '../common/constants';

import {
	copyToClipboard
} from './utils';

const {
	COMPONENT_BOOTSTRAPPED
} = actionTypes;

export default {
	[RESPONSE_TEMPLATES_FETCHED]: responseTemplatesFetched,
	[RESPONSE_TEMPLATES_FETCH_STARTED]: responseTemplatesFetchStarted,
	[RESPONSE_TEMPLATES_FETCH_SUCCEEDED]: responseTemplatesFetchSucceeded,
	[RESPONSE_TEMPLATES_FETCH_FAILED]: responseTemplatesFetchFailed,

	[SEARCH_INPUT_CHANGED]: ({ dispatch, action, properties, updateState }) => {
		const { payload: { fieldValue }} = action;
		updateState({ loading: true });
		dispatch(RESPONSE_TEMPLATES_FETCH_STARTED, {
			data: {
				tableName: properties.table,
				recordId: properties.sysId,
				searchTerm: fieldValue,
				limit: 50,
				offset: 0,
				includeEvaluatedBody: true,
				opts: {
					channel: properties.channel
				}
			}
		});
	},

	[NOW_CARD_CLICKED]: ({ action, updateState }) => {
		const { payload: { fullViewTemplateIndex }} = action;
		updateState({
			isFullView: true,
			fullViewTemplateIndex
		});
	},

	[COPY_TO_CLIPBOARD]: ({ action, dispatch }) => {
		const { payload: { templateBody }} = action;

		copyToClipboard(templateBody)
			.then(() => {
				dispatch('ADD_NOTIFICATIONS', {
					notifications: [
						{
							type: 'info',
							message: t('Copied to clipboard!')
						}
					]
				});
			})
			.catch(err => {
				console.error(err);
			});
	},

	[NOW_CARD_HEADER_ACTION_CLICKED]: ({ action, dispatch }) => {
		const { payload: { action: { templateBody }}} = action;
		dispatch(COPY_TO_CLIPBOARD, { templateBody });
	},

	[COMPONENT_BOOTSTRAPPED]: handleComponentBootstrapped
};
