import { createElement, Fragment } from '@servicenow/ui-renderer-snabbdom';

import '@servicenow/now-card';
import '@servicenow/now-input';
import '@servicenow/now-heading';
import '@servicenow/now-button';
import '@servicenow/now-loader';

import { t } from 'sn-translate';

import { stripTags } from '../common/util';
import { COPY_TO_CLIPBOARD } from './constants';

const responseTemplateList = templates => {
	return templates.map((template, i) => {
		const templateBody = template.evaluated_response.evaluated_body;

		return (
			<now-card
				interaction="click"
				append-to-payload={{ fullViewTemplateIndex: i }}
			>
				<now-card-header
					tagline={{ 'label': 'Response', icon: 'chat-outline' }}
					heading={{label: template.name}}
					dropdowns={[{
						id: 'copy_to_clipboard',
						icon: 'ellipsis-v-outline',
						items: [{
							id: 'copy_to_clipboard',
							label: t('Copy to clipboard'), // translation isn't working on this value, need to investigate
							templateBody
						}]
					}]}
				/>
				<div className="rt--card-content">
					{stripTags(templateBody)}
				</div>
			</now-card>
		);
	});
};

const responseTemplateFullView = (templates, fullViewTemplateIndex, dispatch, updateState) => {
	const template = templates[fullViewTemplateIndex];
	const templateBody = template.evaluated_response.evaluated_body;

	const nextTemplateIndex = fullViewTemplateIndex + 1;
	const previousTemplateIndex = fullViewTemplateIndex - 1;

	return (
		<div className="rt--full-view">
			<div className="rt--full-view-header">
				<now-button-iconic
					icon="chevron-start-fill"
					bare
					on-click={() => {
						updateState({
							isFullView: false,
							fullViewTemplateIndex: null
						});
					}}
					aria-label={t('Back')}
				/>
				<div className="rt--full-view-header-actions">
					<now-button-bare
						hidePadding
						label={t("Copy to clipboard")}
						size="sm"
						on-click={() => {
							dispatch(COPY_TO_CLIPBOARD, {
								templateBody
							});
						}}
					/>
				</div>
			</div>
			<div className="rt--full-view-body">
				<now-heading label={template.name} level="3" variant="title-tertiary" />
				{createElement('div',
					{
						props: {
							innerHTML: template.evaluated_response.evaluated_body,
							className: 'rt--full-view-body-content'
						}
					}
				)}
			</div>

			<now-button-iconic
				icon="chevron-left-fill"
				className="rt--full-view-control-previous"
				bare
				disabled={!templates[previousTemplateIndex]}
				on-click={() => {
					updateState({
						fullViewTemplateIndex: previousTemplateIndex
					});
				}}
				aria-label={t('Previous')}
			/>
			<now-button-iconic
				icon="chevron-right-fill"
				className="rt--full-view-control-next"
				bare
				disabled={!templates[nextTemplateIndex]}
				on-click={() => {
					updateState({
						fullViewTemplateIndex: nextTemplateIndex
					});
				}}
				aria-label={t('Next')}
			/>
		</div>
	);
};

const searchInput = () => (
	<now-input type="text">
		<now-icon icon="magnifying-glass-fill" size="md" slot="start" />
	</now-input>
);

const loader = () => <now-loader label={t('Loading response templates...')} />;

const responseTemplateListView = (templates, loading) => {
	return (
		<Fragment>
			<div className="rt--panel-header">
				<now-heading label={t('Response Templates')} level="3" variant="header-secondary" />
				{searchInput()}
			</div>
			<div className="rt--result-list">
				{
					loading
						? loader()
						: responseTemplateList(templates)
				}
			</div>
		</Fragment>
	);
}

export default (state, { dispatch, updateState }) => {
	const {
		templates,
		isFullView,
		fullViewTemplateIndex,
		loading
	} = state;

	return (
		<div className="rt--container">
			{
				isFullView && fullViewTemplateIndex != null
					? responseTemplateFullView(templates, fullViewTemplateIndex, dispatch, updateState)
					: responseTemplateListView(templates, loading, updateState)
			}
		</div>
	);
};
