import '@servicenow/now-card';

import { stripTags } from '../common/util';
import { t } from 'sn-translate';

const responseTemplateList = templates => {
	return templates.map(t => {
		const templateBody = t.evaluated_response.evaluated_body;

		return (
			<now-card
				interaction="click"
				append-to-payload={{ template: templateBody }}
			>
				<now-card-header heading={{label: t.name}} />
				<div className="rt--card-content">
					{stripTags(templateBody)}
				</div>
			</now-card>
		);
	});
};

export default (state, { dispatch }) => {
	const { templates } = state;

	if (!templates.length)
		return;

	return (
		<div
			className="rt--container"
		>
			<div className="rt--result-list">
				{responseTemplateList(templates, dispatch)}
			</div>
		</div>
	);
};
