import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import actions from './actions';
import actionHandlers from './action-handlers';
import styles from './styles.scss';

createCustomElement('sn-response-templates-compact', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		/**
		 * Table name of the record to search against
		 *
		 * @type {string}
		 */
		table: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Record sys_id
		 *
		 * @type {string}
		 */
		sysId: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Search term to pass to response template API
		 *
		 * @type {string}
		 */
		search: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Channel to use for lookup
		 *
		 * @type {string}
		 */
		channel: {
			default: '',
			schema: {
				type: 'string'
			}
		},
	},
	initialState: {
		templates: []
	},
	actions,
	actionHandlers,
	styles
});
